.l-footer{
	position: fixed;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1;
	@include mq($desk-lrg) {
		position: static;
		text-align: center;
	    font-size: 10px;

	}

	@include mq($mob-lrg) { p { margin: 0; } }

	&__wrap {
		width: 100%;

		&--nav-wrap {
			background: $quinaryColor;
			padding-bottom: 40px;
		}

		&--top { background: $quaternaryColor; }

		&--bottom {
			background: $tertiaryColor;
			p,a {
				color: $white;
				text-transform: uppercase;
				margin: 10px 0;
				display: inline-block;
			}
		}
	}

	a{ text-decoration: none; }

	a, p {
		@include font-size($zeta-size, 24);
		font-weight: $light;
	}


	&__content{

		padding: 20px 0 40px 0;
		@include mq($mob-lrg) { padding: 0; }

		.icon {
			position: relative;
			top: 3px;
			font-size: 20px;
			color: $tertiaryColor;

		}
	}
	&__social{
		text-align: right;

		@include mq($tab) { text-align: center; }

		.icon{
			margin-left: 0;
			:hover{ text-decoration: none; }
		}
	}

	&__nav {

		list-style: none;
    	padding: 0;
    	margin: 0;

		ul { padding: 0; }

    	&__item {
			float: left;
			width: 20%;
    		font-family:$secondaryFont;
    		line-height: 34px;
			@include mq($tab) { width: 33.3%}
			@include mq($mob-lrg) { width: 100%}
		}

		&__link {
			color: $tertiaryColor;
			text-transform: uppercase;
			font-size: 14px;
			letter-spacing: 1px;
		}

		&__list--submenu {

			.l-footer__nav__item {

				margin-left: 10px;
				margin-left: 10px;
				width: 100%;
				margin: 1px 0;
				padding-bottom: 0;
				width: 100%;
				list-style: none;
				list-style-position: outside;
				line-height: 20px;

				.l-footer__nav__link {
					color: $primaryColor;
					font-family: $primaryFont;
					text-transform: none;

				}
			}

		}

	}



}

.siteby{
	text-transform: uppercase;
	font-size: 10px;

