.clear:after {
     visibility: hidden;
     display: block;
     font-size: 0;
     content: " ";
     clear: both;
     height: 0;
     }
.clear { display: inline-block; }
/* start commented backslash hack \*/
* html .clear { height: 1%; }
.clear { display: block; }
/* close commented backslash hack */


/* For image replacement */
.ir { display: block; text-indent: -999em; overflow: hidden; background-repeat: no-repeat; text-align: left; direction: ltr; }
.ir br { display: none; }

/* Hide for both screenreaders and browsers:
   css-discuss.incutio.com/wiki/Screenreader_Visibility */
.hidden { display: none; visibility: hidden!important; }

/* Hide only visually, but have it available for screenreaders: by Jon Neal.
  www.webaim.org/techniques/css/invisiblecontent/  &  j.mp/visuallyhidden */
.visuallyhidden { border: 0; clip: rect(0 0 0 0); height: 1px; margin: -1px; overflow: hidden; padding: 0; position: absolute; width: 1px; }

/* Extends the .visuallyhidden class to allow the element to be focusable when navigated to via the keyboard: drupal.org/node/897638 */
.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus { clip: auto; height: auto; margin: 0; overflow: visible; position: static; width: auto; }

/* Hide visually and from screenreaders, but maintain layout */
.invisible { visibility: hidden; }
.js .is-hidden {display:none;}
.is-visible {display:block;}


/* Floats/Clears */
.fl {float: left!important; display:inline; }
.fr {float: right!important; display:inline;}

.clear {
	&--left { clear: left; }
	&--right { clear: right; }
	&--both { clear: both; }
	&--none { clear: none; }
}



.ib {@include inline-block;}
.relative{position: relative;}



/* Alignments */

// Layout
%layout--centre { margin-left:auto!important; margin-right:auto!important; display:block!important; float:none!important; }
.l-align {
	&--left { float: left; }
	&--right { float: right; }
	&--centre { @extend %layout--centre; }
	&--bottom { @include inline-block(bottom); float: none!important;}

	&--vertical {
		&--top { vertical-align: top; }
		&--mid { vertical-align: middle; }
		&--bottom { vertical-align: bottom; }
	}
}

// Reset Shit
.reset {
	&--margin { margin: 0 !important; }
	&--padding { padding: 0 !important; }
	&--height { height: auto; }
	&--all { line-height:0; margin:0; padding:0; height:0; font-size:0; }
}


// text based hooks
.text {
	&--strong { font-weight: bold; }
	&--normal { font-weight: normal; }
	&--italic { font-style: italic; }
	&--underline { text-decoration: underline; }
	&--upper { text-transform: uppercase; }
	&--lower { text-transform: lowercase; }
	&--reset { text-transform: none; text-decoration: none; }
	&--left { text-align:left; }
	&--right { text-align: right; }
	&--centre { text-align: center; }
}

//overflows
.overflow {
	&--hid { overflow: hidden; }
	&--vis { overflow: visible; }
	&--auto { overflow: auto; }
	&--scroll { overflow: scroll; }
}


// Borders
.border {
	border: 1px solid $primaryColor;

	&--top { border-top: 1px solid $primaryColor; }
	&--right { border-right: 1px solid $primaryColor; }
	&--bottom { border-bottom: 1px solid $primaryColor; }
	&--left { border-left: 1px solid $primaryColor; }

	&--none { border: 0px solid transparent; }
}



